import React from 'react';

import { Link } from 'react-router-dom';

import Articles from './articles';
import Contact from './contact';

import { basePath } from '../../../App';


export default class Blog extends React.Component {
  /**
   * url 1 
   * @type {string}
   */
  paramsPage = '1';

  /**
   * CompositionEvent compositionstart  truecompositionend  false
   * @type {boolean}
   */
  isComposition = false;

  componentDidMount() {
    // this.props.paramsPage /  undefined /page/2 
    if (this.props.paramsPage !== undefined) {
      this.paramsPage = this.props.paramsPage;
      /*
      /page/2  this.props.setPage(Number(this.paramsPage)); 
      loadArticlesFromServer()  componentDidUpdate() 
      if (this.props.page !== Number(this.paramsPage)) {  this.props.setPage(Number(this.paramsPage)); 
      /page/2 componentDidUpdate()  this.props.page  1this.paramsPage  '2'
      url  /page/2 this.props.loadArticlesFromServer() 
      */
    }

    // url  ID 
    if (this.props.paramsArticleId === undefined) {
      this.props.loadArticlesFromServer('', this.props.selectedCategoryId, this.props.searchWord, this.paramsPage);
    // url  ID  1 
    } else {
      this.props.loadArticlesFromServer(this.props.paramsArticleId, this.props.selectedCategoryId, this.props.searchWord);
    }

    // 
    this.props.loadCategoriesFromServer();

    // 
    window.addEventListener('resize', () => {
      var menu = document.getElementById('menu');
      var display = document.defaultView.getComputedStyle(menu, null).display;
      if (display === 'block') {
        menu.style.display = 'none';
      }
    });

    // iOS  change CompositionEvent 
    var searchInput = document.getElementById('search').childNodes[0];
    searchInput.addEventListener('compositionstart', (e) => {
      this.isComposition = true;
    });
    // searchInput.addEventListener('compositionupdate', (e) => {
    // });
    searchInput.addEventListener('compositionend', (e) => {
      this.search(e);
      this.isComposition = false;
    });
  }

  componentDidUpdate(prevProps, prevState) {
    // this.props.paramsPage /  undefined /page/2 
    if (this.props.paramsPage === undefined) {
      this.paramsPage = '1';
    } else {
      this.paramsPage = this.props.paramsPage;
    }
    // this.props.page  statestate.showArticlesReducer.page
    if (this.props.page !== Number(this.paramsPage)) {
      this.props.loadArticlesFromServer('', this.props.selectedCategoryId, this.props.searchWord, this.paramsPage);
      this.props.setPage(Number(this.paramsPage));
    }
  }

  /**
   * 
   * @param {SytheticEvent} e React 
   */
  search(e) {
    // compositionend  change 
    if (this.isComposition === true && e.type === 'change') {
      return;
    }
    if (e.target.validationMessage) {
      // do nothing
    } else {
      if (this.props.paramsArticleId === undefined) {
        this.props.loadArticlesFromServer('', this.props.selectedCategoryId, this.searchWord.value.trim());
        this.props.history.push(basePath);
      } else {
        this.props.loadArticlesFromServer(this.props.paramsArticleId, this.props.selectedCategoryId, this.searchWord.value.trim());
        this.props.history.push(basePath + 'articles/' + this.props.paramsArticleId);
      }
      this.props.setPage(1);     // 1 
    }
  }

  /**
   * 
   * @param {number} categoryId blog_category  ID
   * @param {Object} props      this.props
   * @param {Object} searchWord 
   */
  categoryFilter(categoryId, props, searchWord) {
    if (props.selectedCategoryId !== '') {
      document.getElementById('category-' + props.selectedCategoryId).style.background = 'white';
    }
    document.getElementById('category-' + categoryId).style.background = '#DBFFDB';
    searchWord.value = '';
    props.loadArticlesFromServer('', String(categoryId));
    props.setPage(1);     // 1 
  }

  /**
   * BLOG by NUAGES 
   */
  initPage() {
    if (this.props.selectedCategoryId !== '') {
      document.getElementById('category-' + this.props.selectedCategoryId).style.background = 'white';
    }
    this.searchWord.value = '';
    this.props.loadArticlesFromServer();
    this.props.setPage(1);
  }

  /**
   * 
   */
  showAndHideMenu() {
    var menu = document.getElementById('menu');
    var display = document.defaultView.getComputedStyle(menu, null).display;
    if (display === 'block') {
      menu.style.display = 'none';
    } else {
      menu.style.display = 'block';
    }
  }

  /**
   * ABOUTCATEGORYCONTACT
   * @param {string} id  id 
   */
  scroll(id) {
    //  (0, 0) 
    var clientRect = document.getElementById(id).getBoundingClientRect();
    // window.pageYOffset  
    window.scrollTo(0, window.pageYOffset + clientRect.top - 114);     // header  114px
    var menu = document.getElementById('menu');
    menu.style.display = 'none';
  }

  render() {
    var props = this.props;
    var searchWord = this.searchWord;
    var categoryFilter = this.categoryFilter;

    // 
    var categoriesForReturn = props.categories.map(function(category, i) {
      return (
        <span key={i}><Link to={basePath} onClick={() => categoryFilter(category.id, props, searchWord)} id={'category-' + category.id}>{category.name}</Link>&nbsp;&nbsp;</span>
      );
    });

    // 
    var numbers = [];
    var maxNumber = Math.floor(props.articlesTotalCount / 5);
    if (props.articlesTotalCount % 5 !== 0) {
      maxNumber += 1;
    }
    for (var i=0; i<maxNumber; i++) {
      numbers.push(i + 1);
    }
    var pageLinks = numbers.map(function(number, i) {
      return <span key={i}>&nbsp;&nbsp;<Link to={basePath + 'page/' + number}>{number}</Link>&nbsp;&nbsp;</span>;
    });

    return (
      <div>
        <header>
          <div id="header-left">
            <Link to={basePath} onClick={() => this.initPage()}><span>BLOG</span>&nbsp;<span id="bytakaohayashi">by Takao Hayashi</span></Link>
            <div id="menu-button-wrap">
              <span className="fa-stack fa-2x">
                <i onClick={this.showAndHideMenu} className="fa fa-bars fa-stack-1x fa-inverse"></i>
              </span>
            </div>
          </div>
          <div id="header-right">
            <div id ="search">
              <input type="text" ref={(input) => {this.searchWord = input}} name="search_word" onChange={(e) => this.search(e)} placeholder="Search" />
            </div>
            <div id="icons">
              <a href="" target="_blank" rel="noreferrer noopener"><img src={basePath + 'assets/images/github-7-64.png'} width="40px" height="40px" alt="github" /></a>&nbsp;&nbsp;
              <a href="" target="_blank" rel="noreferrer noopener"><img src={basePath + 'assets/images/twitter-2-64.png'} width="40px" height="40px" alt="twitter" /></a>&nbsp;&nbsp;
              <a href="" target="_blank" rel="noreferrer noopener"><img src={basePath + 'assets/images/rss-2-64.png'} width="40px" height="40px" alt="rss" /></a>
            </div>
          </div>
        </header>
        <div id="menu">
          <div onClick={() => this.scroll('about-heading')}>ABOUT</div>
          <div onClick={() => this.scroll('category-heading')}>CATEGORY</div>
          <div onClick={() => this.scroll('contact-heading')}>CONTACT</div>
          <div><a href="" target="_blank" rel="noreferrer noopener">GITHUB</a></div>
          <div><a href="" target="_blank" rel="noreferrer noopener">TWITTER</a></div>
        </div>
        <main>
          <div id="main-wrapper">
            <div id="article-list">
              {/*
              selectedCategoryIdthisSearchWordloadArticlesFromServersetPage 
              Articles  categoryFilter  props 
              */}
              <Articles
                articles={props.articles}
                searchWord={props.searchWord}
                selectedCategoryId={props.selectedCategoryId}
                thisSearchWord={searchWord}
                categoryFilter={categoryFilter}
                loadArticlesFromServer={props.loadArticlesFromServer}
                setPage={props.setPage} />
              {(() => {
                if (props.paramsArticleId === undefined) {
                  return <div id="page-link">{pageLinks}</div>;
                }
              })()}
            </div>
            <aside>
              <h3 className="sidebar-heading" id="about-heading">ABOUT</h3>
              <div id="about"></div>
              <h3 className="sidebar-heading" id="category-heading">CATEGORY</h3>
              <div id="category-list">{categoriesForReturn}</div>
              <h3 className="sidebar-heading" id="contact-heading">CONTACT</h3>
              <Contact sendMail={props.sendMail} />
            </aside>
          </div>
        </main>
        <footer>&#169; 2018 Takao Hayashi</footer>
      </div>
    );
  }
}



