import React from 'react';

import request from 'superagent';

import { apiUrl, basePath } from '../../../App';


export default class Contact extends React.Component {
  /**
   * 
   * @type {Object}
   */
  formStatus = { name: false, fromEmail: false, subject: false, message: false };

  /**
   * Send  Element 
   * @type {Element}
   */
  contactSubmitBtn = null;

  componentDidMount() {
    this.contactSubmitBtn = document.getElementById('contact-submit-btn');
    this.contactSubmitBtn.disabled = true;
    this.contactSubmitBtn.style.cursor = 'not-allowed';
  }

  /**
   * 
   * @param {SytheticEvent} e React 
   * @param {string} propertyName formStatus 
   */
  checkValue(e, propertyName) {
    // formStatus 
    if (e.target.validationMessage) {
      this.formStatus[propertyName] = false;
    } else {
      this.formStatus[propertyName] = true;
    }

    //  disabled 
    for (var key in this.formStatus) {
      if (this.formStatus[key] === false) {
        this.contactSubmitBtn.disabled = true;
        this.contactSubmitBtn.style.cursor = 'not-allowed';
        return;
      }
    }
    this.contactSubmitBtn.disabled = false;
    this.contactSubmitBtn.style.cursor = 'pointer';
  }

  /**
   * 
   * @param {SytheticEvent} e React 
   */
  sendMail(e) {
    e.preventDefault();     // 

    // 
    var sendingImg = document.getElementById('sending-img');
    sendingImg.style.display = 'inline';
    sendingImg.style.verticalAlign = 'middle';

    request
      .post(apiUrl + 'mail/from-contact-form/send')
      .set('Accept', 'application/json')
      .field('name', this.name.value.trim())
      .field('from_email', this.fromEmail.value.trim())
      .field('subject', this.subject.value.trim())
      .field('message', this.message.value.trim())
      .field('site_url', 'https://takao-hayashi.github.io/blog')
      .end(function(err, res) {
        if (res.ok) {
          alert(res.body.result);
        } else {
          console.error('error');
        }
        console.log('complete');
        // 
        sendingImg.style.display = 'none';
      });

    // 
    this.name.value = '';
    this.fromEmail.value = '';
    this.subject.value = '';
    this.message.value = '';
    // formStatus 
    this.formStatus = { name: false, fromEmail: false, subject: false, message: false };
    // 
    this.contactSubmitBtn.disabled = true;
    this.contactSubmitBtn.style.cursor = 'not-allowed';
  }

  render() {
    return (
      <form id="contact">
        <div>
          <input type="text" ref={(ref) => {this.name = ref}} name="name" placeholder="Name" required onChange={(e) => this.checkValue(e, 'name')} />
        </div>
        <div>
          <input type="email" ref={(ref) => {this.fromEmail = ref}} name="from_email" placeholder="Email" required pattern="[\w\d_-]+@[\w\d_-]+\.[\w\d._-]+" onChange={(e) => this.checkValue(e, 'fromEmail')} />
        </div>
        <div>
          <input type="text" ref={(ref) => {this.subject = ref}} name="subject" placeholder="Subject" required onChange={(e) => this.checkValue(e, 'subject')} />
        </div>
        <div>
          <textarea ref={(ref) => {this.message = ref}} name="message" placeholder="Message" required onChange={(e) => this.checkValue(e, 'message')}></textarea>
        </div>
        <div>
          <button onClick={(e) => this.sendMail(e)} id="contact-submit-btn">Send</button>&nbsp;&nbsp;&nbsp;
          <img src={basePath + 'assets/images/sending.gif'} width="25px" height="25px" alt="sending" id="sending-img" />
        </div>
      </form>
    );
  }
}



