import { SET_ARTICLES_TOTAL_COUNT, SET_CATEGORY_ID, SET_PAGE, SET_SEARCH_WORD, SHOW_ARTICLES } from '../actions';


const initialState = {
  articles: [],
  articlesTotalCount: 0,
  page: 1,
  searchWord: '',
  selectedCategoryId: ''
};

export default function showArticlesReducer(state = initialState, action) {
  switch (action.type) {
    case SET_ARTICLES_TOTAL_COUNT:
      return Object.assign({}, state, {
        articlesTotalCount: action.articlesTotalCount,
      });
    case SET_CATEGORY_ID:
      return Object.assign({}, state, {
        selectedCategoryId: action.selectedCategoryId,
      });
    case SET_PAGE:
      return Object.assign({}, state, {
        page: action.page,
      });
    case SET_SEARCH_WORD:
      return Object.assign({}, state, {
        searchWord: action.searchWord,
      });
    case SHOW_ARTICLES:
      return Object.assign({}, state, {
        articles: action.articles,
      });
    default:
      return state;
  }
}



