import React from 'react';

import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import Blog from '../presentation/public/blog';

import { basePath } from '../../App';


export default class Nuages extends React.Component {
  /**
   * Blog 
   * @param  {Object} thisProps  this.props
   * @param  {Object} routeProps Route  props
   * @return {ReactElement} Blog 
   */
  makeBlog(thisProps, routeProps) {
    return (
      <Blog
        articles={thisProps.articles}
        articlesTotalCount={thisProps.articlesTotalCount}
        categories={thisProps.categories}
        history={routeProps.history}
        page={thisProps.page}
        paramsArticleId={routeProps.match.params.articleId}
        paramsPage={routeProps.match.params.page}
        searchWord={thisProps.searchWord}
        selectedCategoryId={thisProps.selectedCategoryId}
        loadArticlesFromServer={thisProps.loadArticlesFromServer}
        loadCategoriesFromServer={thisProps.loadCategoriesFromServer}
        sendMail={thisProps.sendMail}
        setPage={thisProps.setPage} />
    );
  }

  render() {
    return (
      <Router>
        <Switch onChange={window.scrollTo(0, 0)}>     {/*  */}
          <Route exact path={basePath} render={(props) => this.makeBlog(this.props, props)} />
          <Route path={basePath + 'page/:page'} render={(props) => this.makeBlog(this.props, props)} />
          <Route path={basePath + 'articles/:articleId'} render={(props) => this.makeBlog(this.props, props)} />
        </Switch>
      </Router>
    );
  }
}



