export const SET_ARTICLES_TOTAL_COUNT = 'SET_ARTICLES_TOTAL_COUNT';

export function setArticlesTotalCount(articlesTotalCount) {
  return {
    type: SET_ARTICLES_TOTAL_COUNT,
    articlesTotalCount,
  };
}


export const SET_CATEGORY_ID = 'SET_CATEGORY_ID';

export function setCategoryId(selectedCategoryId) {
  return {
    type: SET_CATEGORY_ID,
    selectedCategoryId,
  };
}


export const SET_PAGE = 'SET_PAGE';

export function setPage(page) {
  return {
    type: SET_PAGE,
    page,
  };
}


export const SET_SEARCH_WORD = 'SET_SEACH_WORD';

export function setSearchWord(searchWord) {
  return {
    type: SET_SEARCH_WORD,
    searchWord,
  };
}


export const SHOW_ARTICLES = 'SHOW_ARTICLES';

export function showArticles(articles) {
  return {
    type: SHOW_ARTICLES,
    articles,
  };
}


export const SHOW_CATEGORIES = 'SHOW_CATEGORIES';

export function showCategories(categories) {
  return {
    type: SHOW_CATEGORIES,
    categories,
  };
}



