import React from 'react';

import { Link } from 'react-router-dom';

import SyntaxHighlighter from 'react-syntax-highlighter';

import { basePath } from '../../../App';

// import { docco as rshStyle } from 'react-syntax-highlighter/dist/styles';     // 
var rshStyle = require('react-syntax-highlighter/dist/styles/hljs/atom-one-light').default;     // 

// import { basePath } from '../../../App';     //  Import in body of module; reorder to top  import/first 


export default class Articles extends React.Component {
  componentDidMount() {
  }

  /**
   * 
   * @param {number}         lineNumber     
   * @param {Array.<number>} highlightLines 
   * @return {Object} 
   */
  setLineStyle(lineNumber, highlightLines) {
    let style = { display: 'block' };
    if (highlightLines.includes(lineNumber)) {
      style.backgroundColor = '#DBFFDB';
    }
    return style;
  }

  /**
   * 
   * @param  {string} input      
   * @param  {string} searchWord 
   * @return {ReactElement} markup
   */
  searchAndHighlight(input, searchWord) {
    var out = input;

    if (searchWord !== '' && input.indexOf(searchWord) !== -1) {
      var splited = input.split(searchWord);
      out = splited.map(function(element, i) {
        if (i !== splited.length - 1) {
          return <span key={i}><span>{element}</span><span className="highlight">{searchWord}</span></span>;
        } else {
          return <span key={i}>{element}</span>;
        }
      });
    }

    return out;
  }

  render() {
    var props = this.props;
    var categoryFilter = this.props.categoryFilter;
    var setLineStyle = this.setLineStyle;
    var searchAndHighlight = this.searchAndHighlight;

    var articlesForReturn = props.articles.map(function(article, i) {
      // 
      var createdAt = article.created_at.slice(0, 10);

      // 
      var title = article.title;
      if (props.searchWord !== '' && title.indexOf(props.searchWord) !== -1) {
        var splited = title.split(props.searchWord);
        title = splited.map(function(element, j) {
          if (j !== splited.length - 1) {
            return <span>{element}<span className="highlight">{props.searchWord}</span></span>;
          } else {
            return <span>{element}</span>;
          }
        });
      }

      // 
      var categoryName = '';
      if (article.category !== null) {
        categoryName = article.category.name;
      }

      // 
      var articleContent = article.content;
      // slicedArticleContents 
      var slicedArticleContents = [];
      while (articleContent.indexOf('[code:') !== -1) {
        var codeStartOpenIndex = articleContent.indexOf('[code:');     // [code:<language>] 
        var first = articleContent.slice(0, codeStartOpenIndex - 2);
        slicedArticleContents.push(first);

        var tmp = articleContent.slice(codeStartOpenIndex);
        var codeEndOpenIndex = tmp.indexOf('[code]');     // [code] 
        var second = tmp.slice(0, codeEndOpenIndex + 6);
        slicedArticleContents.push(second);

        articleContent = tmp.slice(codeEndOpenIndex + 8);
      }
      slicedArticleContents.push(articleContent);

      var contentForReturn = slicedArticleContents.map(function(slicedArticleContent, j) {
        // 
        if (slicedArticleContent.indexOf('[code:') !== -1) {
          var language = slicedArticleContent.slice(slicedArticleContent.indexOf(':') + 1, slicedArticleContent.indexOf(']'));     // 
          var code = slicedArticleContent.slice(slicedArticleContent.indexOf(']') + 3, slicedArticleContent.indexOf('[code]'));
          var highlightLines = [];     // 
          var codeRows = code.split('\n');     // 
          //  highlightLines 
          for (var k=0; k<codeRows.length; k++) {
            if (props.searchWord !== '' && codeRows[k].indexOf(props.searchWord) !== -1) {
              highlightLines.push(k + 1);
            }
          }
          // 
          if (language === '') {
            return (
              <div className="code" key={j}>
                <SyntaxHighlighter
                  style={rshStyle}
                  wrapLines={true}
                  lineProps={(lineNumber) => ({style: setLineStyle(lineNumber, highlightLines)})}
                  CodeTag='plaintext'
                >
                  {code}
                </SyntaxHighlighter>
              </div>
            );
          // 
          } else {
            return (
              <div className="code" key={j}>
                <SyntaxHighlighter
                  language={language}
                  style={rshStyle}
                  wrapLines={true}
                  lineProps={(lineNumber) => ({style: setLineStyle(lineNumber, highlightLines)})}
                >
                  {code}
                </SyntaxHighlighter>
              </div>
            );
          }
        }

        // 
        var textRows = slicedArticleContent.split('\n');     // 
        var textRowsForReturn = textRows.map(function(textRow, k) {
          var isN = false;     // 
          var isH = false;     // 
          var isFn = false;     // 
          if (textRow.indexOf('[n]') !== -1) {
            isN = true;     //  [n]  true 
            textRow = textRow.split('[n]').join('');     //  [n]  [n] 
          }
          if (textRow.indexOf('[h]') !== -1) {
            isH = true;     //  [h]  true 
            textRow = textRow.split('[h]').join('');     //  [h]  [h] 
          }
          if (textRow.indexOf('[fn]') !== -1) {
            isFn = true;     //  [fn]  true 
            textRow = textRow.split('[fn]').join('');     //  [fn]  [fn] 
          }

          var textRowForReturn = [];
          var l = 0;
          while (textRow.indexOf('[link:') !== -1) {
            var linkStartOpenIndex = textRow.indexOf('[link:');     // [link:<url>] 
            var first = textRow.slice(0, linkStartOpenIndex);
            // textRow  first[link:<url>] 
            first = searchAndHighlight(first, props.searchWord);
            textRowForReturn.push(first);

            var tmp = textRow.slice(linkStartOpenIndex);
            var linkStartCloseIndex = tmp.indexOf(']');     // [link:<url>] 
            var url = tmp.slice(tmp.indexOf(':') + 1, linkStartCloseIndex);
            var linkEndOpenIndex = tmp.indexOf('[link]');     // [link] 
            var second = tmp.slice(linkStartCloseIndex + 1, linkEndOpenIndex);
            // textRow  second[link:<url>]  [link] 
            second = searchAndHighlight(second, props.searchWord);
            textRowForReturn.push(<a key={l} href={url} target="_blank" rel="noreferrer noopener">{second}</a>);
            l++;

            textRow = tmp.slice(linkEndOpenIndex + 6);
          }

          // textRow 
          // while (textRow.indexOf('[link:') !== -1)  [link:  textRow 
          textRow = searchAndHighlight(textRow, props.searchWord);
          textRowForReturn.push(textRow);

          if (isN === true) {
            return <div className="nl" key={k}></div>;
          } else if (isH === true) {
            return <h3 className="article-row" key={k}>{textRowForReturn}</h3>;
          } else if (isFn === true) {
            return <div className="article-row filename" key={k}>{textRowForReturn}</div>;
          } else {
            return <div className="article-row" key={k}>{textRowForReturn}</div>;
          }
        });

        return <div key={j}>{textRowsForReturn}</div>;
      });

      return (
        <div className="article-wrapper" key={i}>
          <div className="created-at">{createdAt}</div>
          <h2>{title}</h2>
          <div className="category"><Link to={basePath} onClick={() => categoryFilter(article.category.id, props, props.thisSearchWord)}>{categoryName}</Link></div>
          <div className="content">{contentForReturn}</div>
        </div>
      );
    });

    return <div>{articlesForReturn}</div>
  }
}



