import { SHOW_CATEGORIES } from '../actions';


const initialState = {
  categories: []
};

export default function showCategoriesReducer(state = initialState, action) {
  switch (action.type) {
    case SHOW_CATEGORIES:
      return Object.assign({}, state, {
        categories: action.categories,
      });
    default:
      return state;
  }
}



